// basic-styling
@import 'component/styling/normalize';
@import 'component/styling/color';
@import 'component/styling/basic';
@import 'component/styling/layout';

// general-component
@import "component/footer";
@import "component/header";

// purpose-component
@import "component/project-banner";

// hover-effect
@import "component/styling/hover";

.clip-path {
    display: none;
}

.top-btn {
    right: 100px;
    bottom: 100px;
    position: fixed;
    height: 50px;
    width: 50px;
    z-index: 100;
    visibility: visible;
    opacity: 1;
    transition-duration: 0.5s;

    >div {
        height: 100%;
        width: 100%;
        border-radius: 50%;
        border: 2px solid #ec6c1f;
    }

    svg {
        height: 80%;
        width: 80%;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &.top {
        right: 50vw;

        transform: translate(65%, 0) rotate(180deg);
    }
}


.visual {
    height: 85vh;
    position: relative;

    
    .visual-wrapper {
        position: absolute;
        width: 200px;
        height: 200px;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        
        > * {
            position: absolute;
            top: 0;
            left: 0;
        }
    }

    .visual-border {

    }

    .visual-clip {
        clip-path: url(#clip-path);
        background-color: #ffffff;
        height: 100%;
        width: 100%;
        opacity: 0;
        transform: scale(1.5);
        .mask {
            background-color: rgb(223, 106, 106);
        }
    }
}

.projects {
    @include sepration-line;

    .more {
        text-align: right;
        width: 100%;

        h3 {
            margin: 10px 0;
        }
    }

    .filter:hover > img {
        filter: grayscale(0%);
    
    }
}

.more {}


.works {
    @include sepration-line;

    .work-container {
        // width: 100%;
        margin-left: -5px;
        margin-right: -5px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 300px 300px 300px 300px;
        grid-template-areas:
            "w2h1-1 w2h1-1 w1h2-2"
            "w1h1-3 w1h1-4 w1h2-2"
            "w1h1-5 w2h2-6 w2h2-6"
            "w1h1-7 w2h2-6 w2h2-6";

        @mixin work-item-block($order, $gArea) {
            >figure:nth-child(#{$order}) {
                grid-area: $gArea;
            }
        }

        @include work-item-block(1, w2h1-1);
        @include work-item-block(2, w1h2-2);
        @include work-item-block(3, w1h1-3);
        @include work-item-block(4, w1h1-4);
        @include work-item-block(5, w1h1-5);
        @include work-item-block(6, w2h2-6);
        @include work-item-block(7, w1h1-7);

    }

    .work__item {
        margin: 5px;

        >div {
            height: 100%;
            width: 100%;
            background-color: rgb(231, 123, 123);
            position: relative;

            img {
                height: 100%;
                width: 100%;
                object-fit: cover;
                object-position: 50% 50%;
            }
        }
    }

    .work-img {
        position: relative;

        img {
            object-fit: cover;
            object-position: 50% 50%;
            height: 100%;
        }
    }
}

.brands {
    >div {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 0 10%;
    }

    .brands-logo {
        position: relative;
        flex: auto 1 1;
        margin: 1vw;

        &::after {
            position: absolute;
            content: "";
            display: block;
            padding-bottom: 100%;
        }

        >img {
            height: 100%;
            width: 100%;
        }
    }
}